import { Avatar } from "@chakra-ui/avatar";
import { Box, Text } from "@chakra-ui/layout";
// import { ChatState } from "../../context/chatProvider";
// UserListItem.js

import { useHistory } from "react-router-dom";

const UserListItem = ({ user, handleFunction }) => {
  const history = useHistory();

  const redirectToChat = () => {
    // Redirect to chat or execute the desired logic
    if (handleFunction) {
      handleFunction(user._id);
    } else {
      // Default behavior, e.g., redirect to user profile
      history.push(`/profile/${user._id}`);
    }
  };

  return (
    <Box
      onClick={redirectToChat}
      cursor="pointer"
      bg="#E8E8E8"
      _hover={{
        background: "#38B2AC",
        color: "white",
      }}
      w="100%"
      d="flex"
      alignItems="center"
      color="black"
      px={3}
      py={2}
      mb={2}
      borderRadius="lg"
    >
      <Avatar
        mr={2}
        size="sm"
        cursor="pointer"
        name={user.name}
        src={user.pic}
      />
      <Box>
        <Text>{user.name}</Text>
        <Text fontSize="xs">
          <b>Email : </b>
          {user.email}
        </Text>
      </Box>
    </Box>
  );
};

export default UserListItem;
